  border-radius:10px;
  /* z-index: 100; */
}

.mdl-layout__header-row .mdl-navigation__link:hover{
  box-shadow: inset 5px 5px 5px rgba(0, 0, 0, 0.07), inset -5px -5px 5px rgba(255, 255, 255, .9);
}


