/* Contact Me CSS */

.contact-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  position: relative;
  width: 50%;
  height: 80%;
  /* background: white; */
  background: rgba(247, 224, 186, 0.20);
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: 'Cutive Mono', monospace;
}

.contact-grid p {
  font-family: 'Cutive Mono';
  font-weight: bold;
  padding-bottom: 10px !important;

}

.contact-grid hr {
  border-top: 5px dotted rgba(240, 202, 139,1);
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 50px;
  padding-right: 3rem;
}

.contact-list {
  justify-content: center;
  display: flex;
}

.contact-list ListItemContent {
  justify-content: center;
}

.mdl-tabs__tab-bar{
  border-bottom: 4px dotted rgba(245,216,166, .45) !important ;
  padding-bottom: 20px !important;
  margin-bottom: 5px !important;
}

.mdl-tabs.is-upgraded .mdl-tabs__tab.is-active:after {
  /* background: rgba(245,216,166, .45) !important; */
  /* background-color:rgba(17,99,4,1)!important; */
  background-color:#fffaf2 !important;
  
  margin-bottom: 5px;
}

.mdl-shadow--16dp {
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.07), -5px -5px 5px rgba(255, 255, 255, .9) !important;
}

.mdl-card{
